<template>
  <div id="quest-view">
    <div class="toolbar">
      <p>篩選：</p>
      <label>
        <input type="checkbox" v-model="filter" value="梅花">
        <p>梅花</p>
      </label>
      <label>
        <input type="checkbox" v-model="filter" value="方塊">
        <p>方塊</p>
      </label>
      <label>
        <input type="checkbox" v-model="filter" value="紅心">
        <p>紅心</p>
      </label>
      <label>
        <input type="checkbox" v-model="filter" value="黑桃">
        <p>黑桃</p>
      </label>
    </div>
    <div class="item" v-for="(quest, index) in filterQuestArray" :key="index">
      <div class="img">
        <img :src="require(`@/assets/image/quest/${quest.i}`)" :alt="quest.q">
      </div>
      <div class="content">
        <p class="q">{{quest.no}}: {{ quest.q }}</p>
        <div class="options">
          <p>答：</p>
          <p v-for="o in quest.o" :key="o.value" :class="{'right': o.value === quest.a}">{{ o.label }}</p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: ['allQuestArray'],
  data () {
    return {
      filter: []
    }
  },
  watch: {
    filter (val) {
      console.log(val)
    }
  },
  computed: {
    filterQuestArray () {
      let theList = []
      if (this.filter.length === 0) {
        theList = this.allQuestArray
      } else {
        theList = this.allQuestArray.filter(quest => {
          if (this.filter.indexOf(quest.no.slice(0, 2)) > -1) {
            return quest
          }
        })
      }
      return theList
    }
  }
}
</script>
